<template>
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog  modal-dialog-centered modal-dialog-scrollable">
            <div class="modal-content">
                <div class="modal-header">
                    <h1 class="modal-title fs-5" id="exampleModalLabel" style="align-items: center;">Cp nht thng tin</h1>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"
                        @click="clickCloseUpdateMenu()"></button>
                </div>
                <div class="modal-body">
                    <ul class="nav nav-pills mb-3" id="pills-tab" role="tablist">
                        <li class="nav-item" role="presentation">
                            <button class="nav-link active" id="pills-home-tab" data-bs-toggle="pill"
                                data-bs-target="#pills-home" type="button" role="tab" aria-controls="pills-home"
                                aria-selected="true">Cp nht thng tin c bn</button>
                        </li>
                        <li class="nav-item" role="presentation">
                            <button class="nav-link" id="pills-profile-tab" data-bs-toggle="pill"
                                data-bs-target="#pills-profile" type="button" role="tab" aria-controls="pills-profile"
                                aria-selected="false">i mt khu</button>
                        </li>
                    </ul>
                    <div class="tab-content" id="pills-tabContent">
                        <div class="tab-pane fade show active" id="pills-home" role="tabpanel"
                            aria-labelledby="pills-home-tab" tabindex="0">
                            <Form @submit="updateInfo()" :validation-schema="dataUpdateInfoFormSchema" class="form-sign"
                                ref="formUpdateInfo">
                                <div class="form-group" disable>
                                    <label for="phone">S in thoi</label>
                                    <input type="text" name="" id="phone" :value="this.user.phone" class="form-control"
                                        disabled>
                                    <!-- <Field name="phone" type="text" class="form-control"
                                        :model-value="this.user.phone" />
                                        <ErrorMessage name="phone" class="error-feedback" /> -->
                                </div>
                                <div class="form-group">
                                    <label for="fullName">H v tn</label>
                                    <Field name="fullName" type="text" class="form-control"
                                        v-model="dataUpdateInfo.fullName" />
                                    <ErrorMessage name="fullName" class="error-feedback" />
                                </div>
                                <div class="form-group">
                                    <label>nh i din: </label>
                                    <button type="button" @click="openUploadModal()">
                                        {{ (this.dataUpdateInfo.avatar === '') ? "Upload nh lm nh i din." :
                                            this.nameFileAvatar }}
                                    </button>
                                </div>
                                <button class="btn btn-signup">Cp nht</button>
                            </Form>
                        </div>
                        <div class="tab-pane fade" id="pills-profile" role="tabpanel" aria-labelledby="pills-profile-tab"
                            tabindex="0">
                            <Form @submit="changePassword()" :validation-schema="dataChangePasswordFormSchema"
                                class="form-sign" ref="formChangePassWord">
                                <div class="form-group">
                                    <label for="currentPassword">Mt khu hin ti</label>
                                    <Field name="currentPassword" type="password" class="form-control"
                                        v-model="dataChangePassword.currentPassword" />
                                    <ErrorMessage name="currentPassword" class="error-feedback" />
                                </div>
                                <div class="form-group">
                                    <label for="password">Mt Khu mi</label>
                                    <Field name="password" type="password" class="form-control"
                                        v-model="dataChangePassword.password" />
                                    <ErrorMessage name="password" class="error-feedback" />
                                </div>
                                <div class="form-group">
                                    <label for="confirmPassword">Xc nhn li mt khu mi</label>
                                    <Field name="confirmPassword" type="password" class="form-control"
                                        v-model="dataChangePassword.confirmPassword" />
                                    <ErrorMessage name="confirmPassword" class="error-feedback" />
                                </div>
                                <button class="btn btn-signup">Cp nht</button>
                            </Form>
                        </div>
                    </div>
                </div>
                <!-- <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <button type="button" class="btn btn-primary">Save changes</button>
                </div> -->
            </div>
        </div>
    </div>
    <ul class="menu">
        <li class="menu_item">
            <div class="menu_avatar" :title=this.user.fullName href="#">
                <img :src=this.user.avatar alt="avatar" v-if="statusMenu !== 'notSignIn'" />
                <img src="../assets/redi.svg" alt="avatar" v-else />
            </div>
        </li>
        <li @click="clickChatMenu()" :class="[(statusMenu === 'chat' || statusMenu === 'notSignIn') ? 'active' : '']"
            class="menu_item">
            <div class="icon-message" href="#"><i class="bx bxs-message-rounded-dots"></i></div>
        </li>
        <li @click="clickContactMenu()" class="menu_item" :class="[(statusMenu === 'contacts') ? 'active' : '']"
            v-if="statusMenu !== 'notSignIn'">
            <a class="icon-message"><i class="bx bxs-contact"></i></a>
        </li>
        <li @click="clickUpdateMenu()" data-bs-toggle="modal" data-bs-target="#exampleModal" class="menu_item"
            :class="[(statusMenu === 'update') ? 'active' : '']" v-if="statusMenu !== 'notSignIn'">
            <a class="icon-message"><i class="fa-sharp fa-solid fa-square-pen"></i></a>
        </li>
        <li class="menu_item menu_item-logout" v-if="statusMenu !== 'notSignIn'" @click="signOut()">
            <div class="icon-message"><i class="fa-solid fa-right-from-bracket"></i></div>
        </li>
    </ul>
</template>
  
<script>
import SocketioService from '../services/socketio.service.js';
import { useStore } from '../stores/Store';
import { storeToRefs } from 'pinia'
import * as yup from "yup";
import { Form, Field, ErrorMessage } from "vee-validate";
import AuthService from '../services/auth.service.js';
export default {
    name: 'Menu',
    props: {
        // statusMenu: {type: String, require: true, default: "notSignIn"},
    },
    components: {
        Form,
        Field,
        ErrorMessage,
    },

    mounted() {

    },
    data() {
        let store = useStore();
        const { statusMenu } = storeToRefs(store);
        const { user } = storeToRefs(store);
        const dataUpdateInfoFormSchema = yup.object().shape({
            fullName: yup
                .string()
                .required("H v tn phi c gi tr.")
                .min(8, "H v tn phi t nht 8 k t.")
                .matches('[a-zA-Z]', "H v tn phi t mt k t ch ci.")
                .max(50, "H v tn c nhiu nht 50 k t."),
        });

        const dataChangePasswordFormSchema = yup.object().shape({
            currentPassword: yup
                .string()
                .required("Mt khu phi c gi tr.")
                .min(8, "Mt khu phi t nht 8 k t."),
            password: yup
                .string()
                .required("Mt khu phi c gi tr.")
                .min(8, "Mt khu phi t nht 8 k t."),
            confirmPassword: yup.string().oneOf([yup.ref('password'), null], 'Mt khu phi trng khp.')
        });
        return {
            statusMenu,
            user,
            dataChangePasswordFormSchema,
            dataUpdateInfoFormSchema,
            dataUpdateInfo: {
                fullName: "",
                avatar: ""
            },
            dataChangePassword: {
                currentPassword: "",
                password: "",
                confirmPassword: ""
            },
            nameFileAvatar: ""
        };
    },
    watch: {
        user() {
            this.dataUpdateInfo.fullName = this.user.fullName
        }
    },
    methods: {
        openUploadModal() {
            const widget = window.cloudinary.createUploadWidget(
                {
                    cloud_name: 'dwnunieno',
                    upload_preset: 'ma9pfovj',
                    maxImageFileSize: 10000000,
                    autoMinimize: true,
                    sources: ["local"],
                    clientAllowedFormats: ["image"],
                    maxFiles: 1
                },
                async (error, result) => {
                    if (!error && result && result.event === "success") {
                        console.log('Done uploading..: ', result.info);
                        this.dataUpdateInfo.avatar = result.info.url;
                        console.log(result.info)
                        console.log(this.dataUpdateInfo.avatar);
                        this.nameFileAvatar = result.info.original_filename + "." + result.info.format;
                        await widget.destroy({ removeThumbnails: true })
                            .then(() => {
                                console.log("Widget was destroyed");
                            });
                    }
                }).open();

            // widget.destroy({ removeThumbnails: true })
            //     .then(() => {
            //         console.log("Widget was destroyed");
            //     });

        },
        clickContactMenu() {
            useStore().clickContactMenu();
        },
        clickUpdateMenu() {
            useStore().clickUpdateMenu();
        },
        clickChatMenu() {
            if (this.statusMenu !== 'notSignIn') {
                useStore().clickChatMenu();
            } else {
                alert("Bn phi ng nhp  thc hin chc nng ny !");
            }
        },
        clickCloseUpdateMenu() {
            useStore().clickCloseUpdateMenu();
        },
        async signOut() {
            try {
                if (confirm("Bn mun ng xut khi ti khon ny ?")) {
                    await AuthService.signOut({
                        phone: this.user.phone
                    })
                    SocketioService.disconnect()
                    localStorage.removeItem('token');
                    useStore().signOut();
                    location.reload();
                }
            } catch (error) {
                console.log(error)
            }
        },
        async updateInfo() {
            if (this.dataUpdateInfo.avatar !== "" || this.dataUpdateInfo.fullName !== this.user.fullName) {
                if (this.dataUpdateInfo.avatar === "") {
                    this.dataUpdateInfo.avatar = this.user.avatar;
                    this.nameFileAvatar = "nh i din hin ti."
                }
                await useStore().updateInfo({ ...this.dataUpdateInfo, "_id": this.user._id });
                this.user.avatar = this.dataUpdateInfo.avatar;
                this.dataUpdateInfo.avatar = "" ;
                // setTimeout(this.$refs.formUpdateInfo.resetForm, 100);
            } else {
                alert("Khng c thng tin mi  cp nht !");
            }
        },
        async changePassword() {
            await useStore().changePassword({ ...this.dataChangePassword, "_id": this.user._id });
            setTimeout(this.$refs.formChangePassWord.resetForm, 50);
            this.dataChangePassword = {
                currentPassword: "",
                password: "",
                confirmPassword: ""
            }
        }
    },
    beforeUnmount() {

    }

}
</script>

<style scoped>
form,
form * {
    width: 100%;
}

.form-group {
    padding-bottom: 5px;
}

.form-control {
    outline: none;
    border: 1px solid black;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 18px;
}

.error-feedback {
    font-size: 13px;
    color: red;
}

#btn-requestContact {
    width: 120px;
    background-color: #0091ff;
    border: none;
    color: white;
    border-radius: 3px;
    padding: 8px 3px;
}
</style>