<template>
    <div class="overlay"></div>
    <div class="modal_form-sign">
        <div class="modal_form-sign-heading">
            <h1>ng K</h1>
        </div>
        <div class="icon-close">
            <i class="bx bx-x"></i>
        </div>
        <!--<form action="./index.html" class="form-sign" @submit.prevent="signUp()">
             <div class="input__field">
                <input id="numberphone" type="text" placeholder="S in thoi" />
            </div>
            <div class="input__field">
                <input id="username" type="text" placeholder="H v tn" />
            </div>
            <div class="input__field">
                <input id="password" type="password" placeholder="Mt khu" />
            </div>
            <div class="input__field">
                <input id="password" type="password" placeholder="Xc nhn li mt khu" />
            </div> -->

        <Form @submit="signUp()" :validation-schema="dataSignUpFormSchema" class="form-sign" ref="formSignUp">
            <div class="form-group">
                <label for="fullName">H v tn</label>
                <Field name="fullName" type="text" class="form-control" v-model="dataSignUp.fullName" />
                <ErrorMessage name="fullName" class="error-feedback" />
            </div>
            <div class="form-group">
                <label for="phone">in thoi</label>
                <Field name="phone" type="tel" class="form-control" v-model="dataSignUp.phone" />
                <ErrorMessage name="phone" class="error-feedback" />
            </div>
            <div class="form-group">
                <label for="password">Mt Khu</label>
                <Field name="password" type="password" class="form-control" v-model="dataSignUp.password" />
                <ErrorMessage name="password" class="error-feedback" />
            </div>
            <div class="form-group">
                <label for="confirmPassword">Xc nhn li mt khu</label>
                <Field name="confirmPassword" type="password" class="form-control" v-model="dataSignUp.confirmPassword" />
                <ErrorMessage name="confirmPassword" class="error-feedback" />
            </div>
            <div class="form-group">
                <label>nh i din: </label>
                <button type="button" @click="openUploadModal()">
                    {{ (this.dataSignUp.avatar === '') ? "Upload nh lm nh i din." : this.nameFileAvatar }}
                </button>
            </div>
            <!-- <div class="auth-form-submit bg-white form-group">
                            <button type="submit" class="auth-form-btn-submit cach">NG K</button>
                        </div> -->
            <button class="btn btn-signup">ng k</button>
            <div class="login-signup">
                <span class="text"> c ti khon</span>
                <span href="#" class="text login-link">ng nhp</span>
            </div>
        </Form>

        <!-- </form>  -->
    </div>
    <!-- modal form sign in -->
    <div class="modal_form-login">
        <div class="modal_form-sign-heading">
            <h1>ng Nhp</h1>
        </div>
        <div class="icon-close">
            <i class="bx bx-x"></i>
        </div>
        <!-- <form action="index.html" class="form-sign">
            <div class="input__field">
                <input id="numberphone" type="text" placeholder="S in thoi" />
            </div>
            <div class="input__field">
                <input id="password" type="password" placeholder="Mt khu" />
            </div>
            <button class="btn btn-signup">ng Nhp</button>
        </form> -->
        <Form @submit="signIn()" :validation-schema="dataSignInFormSchema" class="form-sign" ref="formSignIn">
            <div class="form-group">
                <label for="phone">in thoi</label>
                <Field name="phone" type="tel" class="form-control" v-model="dataSignIn.phone" />
                <ErrorMessage name="phone" class="error-feedback" />
            </div>
            <div class="form-group">
                <label for="password">Mt Khu</label>
                <Field name="password" type="password" class="form-control" v-model="dataSignIn.password" />
                <ErrorMessage name="password" class="error-feedback" />
            </div>
            <!-- <div class="auth-form-submit bg-white form-group">
                            <button type="submit" class="auth-form-btn-submit cach">NG K</button>
                        </div> -->
            <button class="btn btn-signup">ng nhp</button>
            <div class="login-signup">
                <span class="text">Bn cha c ti khon? Hy chn</span>
                <span class="text signup-link">ng k</span>
            </div>
        </Form>
    </div>
    <div class="sidebar">
        <div class="sidebar_list">
            <div class="sidebar_item-search" v-if="statusSidebar !== 'notSignIn'">
                <form @submit.prevent="this.findUserByPhone()">
                    <input type="text" placeholder="Tm kim" v-model="this.searchByPhone" />
                </form>
                <i class="bx bx-search"></i>
            </div>

            <template v-if="statusSidebar === 'notSignIn' || statusSidebar === 'chat'">
                <template v-for="(chatRoom, index) in this.chatRooms" :key="index">
                    <div class="sidebar_item" :title=chatRoom.fullNameFriend @click="openChatContent(chatRoom)"
                        :class="[(chatRoom._id === this.currentChatRoom._id) ? 'active' : '', (chatRoom.lastMessage) ? (!chatRoom.lastMessage.seen && chatRoom.lastMessage.sender !== this.user._id ? 'new' : '') : '']">
                        <img :src=chatRoom.avatar alt="" />
                        <i class="fa-solid fa-circle" v-if="chatRoom.online"></i>
                        <div class="sidebar_content">
                            <h3 class="sidebar_name">{{ chatRoom.fullNameFriend }}</h3>
                            <template v-if="chatRoom.lastMessage">
                                <span class="sidebar_message" v-if="chatRoom.lastMessage.type === 'image'">Hnh nh</span>
                                <span class="sidebar_message" v-else>{{ chatRoom.lastMessage.content }}</span>
                            </template>
                        </div>
                    </div>
                </template>

            </template>

            <!-- SignUp - SignIn Button -->
            <div class="content_body-signup" v-show="statusSidebar === 'notSignIn'">
                <button href="#" class="btn btn-sign" id="open-form-dangnhap1">ng K</button>
                <button href="#" class="btn btn-log">ng Nhp</button>
                <div class="note">
                    *Bng cch bm vo phn "Ngi l", bn c th tr chuyn ngay vi ngi l m khng cn ng nhp.
                </div>
            </div>

            <!-- Contacts -->
            <template v-if="statusSidebar.includes('ontact') && !this.checkSearchByPhone">
                <div @click="clickContactsSidebar()">
                    <div class="sidebar_item" :class="[(statusSidebar === 'contacts') ? 'active' : '']">
                        <i class="bx bxs-group"></i>
                        <div class="sidebar_content">
                            <h3 class="sidebar_name">Danh sch bn b</h3>
                        </div>
                    </div>
                </div>
                <div @click="clickRequestContactSidebar() && !this.checkSearchByPhone">
                    <div class="sidebar_item" :class="[(statusSidebar === 'requestContact') ? 'active' : '']">
                        <i class="bx bxs-user-plus"></i>
                        <div class="sidebar_content">
                            <h3 class="sidebar_name">Danh sch kt bn</h3>
                        </div>
                    </div>
                </div>
            </template>


            <div class="sidebar_item" :title=this.foundUserByPhone.fullName
                v-if="this.foundUserByPhone.fullName && this.chatRooms.length < 1">
                <img :src=this.foundUserByPhone.avatar alt="" />
                <div class="sidebar_content">
                    <h3 class="sidebar_name">{{ this.foundUserByPhone.fullName }}</h3>
                </div>
                <button id="btn-requestContact" @click="sendFriendRequest()"
                    v-if="!this.foundUserByPhone.sentFriendRequest">
                    Kt bn
                </button>
                <button id="btn-requestContact" disabled v-else>
                     gi li mi kt bn
                </button>
            </div>
        </div>
    </div>
</template>
  
<script lang="js">
// import SocketioService from '../services/socketio.service.js';
import * as yup from "yup";
import { Form, Field, ErrorMessage } from "vee-validate";
import AuthService from "../services/auth.service"
import SocketioService from "../services/socketio.service";
import { useStore } from '../stores/Store';
import { storeToRefs } from 'pinia'
export default {
    name: 'Sidebar',
    props: {
        // statusSidebar: { type: String, require: true, default: "notSignIn" },
    },
    components: {
        Form,
        Field,
        ErrorMessage,
    },
    mounted() {
        //this.setup_dangNhap_DangKy()
        this.processFormSignInSignUp()
        this.verifyToken()
    },
    data() {
        let store = useStore();
        const { statusSidebar } = storeToRefs(store);
        const { statusContent } = storeToRefs(store);
        const { statusMenu } = storeToRefs(store);
        const { user } = storeToRefs(store);
        const { currentChatRoom } = storeToRefs(store);
        const { foundUserByPhone } = storeToRefs(store);
        const dataSignUpFormSchema = yup.object().shape({
            fullName: yup
                .string()
                .required("H v tn phi c gi tr.")
                .min(8, "H v tn phi t nht 8 k t.")
                .matches('[a-zA-Z]', "H v tn phi t mt k t ch ci.")
                .max(50, "H v tn c nhiu nht 50 k t."),
            password: yup
                .string()
                .required("Mt khu phi c gi tr.")
                .min(8, "Mt khu phi t nht 8 k t."),
            phone: yup
                .string()
                .matches(
                    /((09|03|07|08|05)+([0-9]{8})\b)/g,
                    "S in thoi khng hp l."
                ),
            confirmPassword: yup.string().oneOf([yup.ref('password'), null], 'Mt khu phi trng khp.')
        });

        const dataSignInFormSchema = yup.object().shape({
            password: yup
                .string()
                .required("Mt khu phi c gi tr.")
                .min(8, "Mt khu phi t nht 8 k t."),
            phone: yup
                .string()
                .matches(
                    /((09|03|07|08|05)+([0-9]{8})\b)/g,
                    "S in thoi khng hp l."
                ),
        });
        return {
            statusSidebar,
            statusContent,
            statusMenu,
            dataSignUp: {
                phone: "",
                password: "",
                fullName: "",
                confirmPassword: "",
                avatar: ""
            },
            dataSignIn: {
                phone: "",
                password: "",
            },
            dataSignUpFormSchema,
            dataSignInFormSchema,
            nameFileAvatar: '',
            user,
            searchByPhone: '',
            currentChatRoom,
            foundUserByPhone,
            checkSearchByPhone: false
        };
    },
    watch: {
        searchByPhone() {
            let check = this.searchByPhone.match(/((09|03|07|08|05)+([0-9]{8})\b)/);
            if (!check) {
                this.foundUserByPhone = {
                    fullName: "",
                    _id: "",
                    avatar: "",
                    phone: "",
                    sentFriendRequest: false
                }
                this.checkSearchByPhone = false
            }
        }
    },
    computed: {
        chatRoomsString() {
            let x = this.user.chatRooms.map(chatRoom => {
                if (chatRoom._id === "000") return "";
                console.log(chatRoom.owner)
                const id = (chatRoom.owner[0] === this.user._id) ? chatRoom.owner[1] : chatRoom.owner[0];
                let phone;
                // console.log(chatRoom.owner[0]._id, this.user._id)
                this.user.contacts.forEach(contact => {
                    if (id === contact._id) {
                        phone = contact.phone;
                    }
                })

                return [phone, this.removeAccents(chatRoom.fullNameFriend)].join("");
            })
            console.log(x);
            return x;
        },
        chatRooms() {
            if (this.searchByPhone === "") {
                let stranger = {
                    _id: "000",
                    owner: [],
                    lastMessageDate: "0:0 0-0-0",
                    fullNameFriend: "Ngi l",
                    avatar: "http://res.cloudinary.com/dwnunieno/image/upload/v1679058005/sdrdzt5t2bhs5lvgsflj.png",
                    online: false
                };
                let filteredChatRooms = this.user.chatRooms;
                filteredChatRooms.sort(this.compareFn);
                return [stranger].concat(filteredChatRooms);
            } else {
                // this.statusContent = 'noChatSelected'
                let searchByPhone = this.removeAccents(this.searchByPhone);
                let filteredChatRooms = this.user.chatRooms.filter((chatRoom, index) => {
                    return this.chatRoomsString[index].includes(searchByPhone);
                }).sort(this.compareFn);
                return filteredChatRooms;
            }
        }
    },
    methods: {
        removeAccents(str) {
            str = str.toLowerCase();
            str = str.replace(/||||||||||||||||/g, "a");
            str = str.replace(/||||||||||/g, "e");
            str = str.replace(/||||/g, "i");
            str = str.replace(/||||||||||||||||/g, "o");
            str = str.replace(/||||||||||/g, "u");
            str = str.replace(/||||/g, "y");
            str = str.replace(//g, "d");
            return str;
        },
        compareFn(a, b) {
            // let datetimeA = a.lastMessageDate.split(" ");
            // let datetimeB = b.lastMessageDate.split(" ");

            // let hourA, minuteA, dateA, monthA, yearA, hourB, minuteB, dateB, monthB, yearB;
            // hourA = parseInt(datetimeA[0].split(":")[0]); minuteA = parseInt(datetimeA[0].split(":")[1]);
            // dateA = parseInt(datetimeA[1].split("-")[0]); monthA = parseInt(datetimeA[1].split("-")[1]); yearA = parseInt(datetimeA[1].split("-")[2]);

            // hourB = parseInt(datetimeB[0].split(":")[0]); minuteB = parseInt(datetimeB[0].split(":")[1]);
            // dateB = parseInt(datetimeB[1].split("-")[0]); monthB = parseInt(datetimeB[1].split("-")[1]); yearB = parseInt(datetimeB[1].split("-")[2]);

            // if (yearA !== yearB) return yearB - yearA;
            // if (monthA !== monthB) return monthB - monthA;
            // if (dateA !== dateB) return dateB - dateA;
            // if (hourA !== hourB) return hourB - hourA;
            // if (minuteA !== minuteB) return minuteB - minuteA;
            if (a.lastMessageDate > b.lastMessageDate) return -1;
            else if (a.lastMessageDate < b.lastMessageDate) return 1;
            else return 0;
        },

        processFormSignInSignUp() {
            const btnSign = document.querySelector(".btn-sign");
            const mdalSign = document.querySelector(".modal_form-sign");
            const btnClose = document.querySelectorAll(".icon-close");
            const btnLog = document.querySelector(".btn-log");
            const mdalLog = document.querySelector(".modal_form-login");
            const overlay = document.querySelector(".overlay");
            const signUp = document.querySelector(".signup-link");
            const login = document.querySelector(".login-link");
            // khi click vo button ng k no th modal no m ra v m lun lp ph m
            btnSign.addEventListener("click", function () {
                // click open modal
                mdalSign.classList.add("open");
                overlay.classList.add("open");
            });
            // khi click vo button ng nhp no th modal no m ra v m lun lp ph m
            btnLog.addEventListener("click", function () {
                // click close modal
                mdalLog.classList.add("open");
                overlay.classList.add("open");
            });
            // khi click vo icon close th ng modal li v tt lp ph m
            btnClose.forEach((btn) => {
                btn.addEventListener("click", function () {
                    // click close modal
                    mdalSign.classList.remove("open");
                    mdalLog.classList.remove("open");
                    overlay.classList.remove("open");
                });
            });
            //chuyn i ng nhp v ng k
            signUp.addEventListener("click", function () {
                mdalSign.classList.add("open");
                mdalLog.classList.remove("open");
            });
            login.addEventListener("click", function () {
                mdalLog.classList.add("open");
                mdalSign.classList.remove("open");
            });
        },
        autoSrcollTop() {
            setTimeout(() => {
                const e = document.querySelector(".content_body")
                e.scrollTop = e.scrollHeight * 2;
            }, 500);
        },
        async verifyToken() {
            try {
                const token = localStorage.getItem('token');
                if (token) {
                    const user = await AuthService.verifyToken({ token: token });
                    if (user) {
                        useStore().setInfoUser(user.message);
                        SocketioService.setupSocketFriendConnection(user.message.token, user.message.phone);
                        SocketioService.receiveMessageFriend((err, data) => {
                            console.log(data);
                            if (data.roomID === this.currentChatRoom._id) {
                                data.seen = true;
                                this.currentChatRoom.messages.push(data);
                                SocketioService.seenAllMessage(this.currentChatRoom._id);
                                this.autoSrcollTop();
                            }
                            this.user.chatRooms.forEach((chatRoom, index) => {
                                if (data.roomID === chatRoom._id) {
                                    this.user.chatRooms[index].lastMessage = data;
                                    this.user.chatRooms[index].lastMessageDate = data.createAt;
                                    return
                                }
                            })
                        });
                        useStore().clickChatMenu();
                    }
                }
            } catch (error) {
                console.log(error);
            }
        },
        clickContactsSidebar() {
            useStore().clickContactsSidebar();
        },
        clickRequestContactSidebar() {
            useStore().clickRequestContactSidebar();
        },
        openChatContent(chatRoom) {
            if (chatRoom._id === this.currentChatRoom._id)
                return;
            this.currentChatRoom = { ...chatRoom, messages: [] };
            if (chatRoom._id === "000")
                useStore().chatWithStranger();
            else {
                SocketioService.loadContentChatRoom(chatRoom._id.toString());
                // this.autoSrcollTop()
                this.user.chatRooms.forEach(room => {
                    if (chatRoom._id === room._id && room.lastMessage) {
                        room.lastMessage.seen = true;
                    }
                })
                SocketioService.seenAllMessage(chatRoom._id);
                useStore().switchChatContent();
            }
        },
        openUploadModal() {
            const widget = window.cloudinary.createUploadWidget(
                {
                    cloud_name: 'dwnunieno',
                    upload_preset: 'ma9pfovj',
                    maxImageFileSize: 10000000,
                    autoMinimize: true,
                    sources: ["local"],
                    clientAllowedFormats: ["image"],
                    maxFiles: 1
                },
                async (error, result) => {
                    if (!error && result && result.event === "success") {
                        console.log('Done uploading..: ', result.info);
                        this.dataSignUp.avatar = result.info.url;
                        console.log(result.info)
                        console.log(this.dataSignUp.avatar);
                        this.nameFileAvatar = result.info.original_filename + "." + result.info.format;
                        await widget.destroy({ removeThumbnails: true })
                            .then(() => {
                                console.log("Widget was destroyed");
                            });
                    }
                }).open();

            // widget.destroy({ removeThumbnails: true })
            //     .then(() => {
            //         console.log("Widget was destroyed");
            //     });

        },
        async signUp() {
            try {
                if (this.dataSignUp.avatar === '') {
                    this.dataSignUp.avatar = "https://res.cloudinary.com/dwnunieno/image/upload/v1678895187/ojzoerhuvkv8ealatzqz.png"
                }
                const meg = await AuthService.signUp(this.dataSignUp);
                document.querySelector(".modal_form-sign").classList.remove('open');
                document.querySelector(".modal_form-login").classList.add('open');
                //document.getElementById("Modaldangky").style.display = "none";
                //document.getElementById("Modaldangnhap").style.display = "block";
                this.dataSignUp = {
                    phone: "",
                    password: "",
                    fullName: "",
                    address: "",
                    avatar: ""
                };
                setTimeout(this.$refs.formSignUp.resetForm, 1500);
                if (meg) alert(meg.message);
            } catch (error) {
                alert(error.response.data.message.toString())
                if(this.dataSignUp.avatar === "https://res.cloudinary.com/dwnunieno/image/upload/v1678895187/ojzoerhuvkv8ealatzqz.png"){
                    this.dataSignUp.avatar = "";
                }
                // console.log(error)
            }
        },
        async signIn() {
            try {
                const meg = await AuthService.signIn(this.dataSignIn);
                useStore().setInfoUser(meg.message)
                SocketioService.setupSocketFriendConnection(meg.message.token, meg.message.phone);
                document.querySelector(".modal_form-login").classList.remove('open');
                document.querySelector(".overlay").classList.remove('open');
                useStore().clickChatMenu();
                // document.querySelector(".modal_form-login").classList.add('open');
                // //document.getElementById("Modaldangky").style.display = "none";
                // //document.getElementById("Modaldangnhap").style.display = "block";
                this.dataSignIn = {
                    phone: "",
                    password: "",
                };
                SocketioService.receiveMessageFriend((err, data) => {
                    console.log(data);
                    if (data.roomID === this.currentChatRoom._id) {
                        data.seen = true;
                        this.currentChatRoom.messages.push(data);
                        SocketioService.seenAllMessage(this.currentChatRoom._id);
                        this.autoSrcollTop()
                    }
                    this.user.chatRooms.forEach((chatRoom, index) => {
                        if (data.roomID === chatRoom._id) {
                            this.user.chatRooms[index].lastMessage = data;
                            this.user.chatRooms[index].lastMessageDate = data.createAt;
                            return
                        }
                    })
                });
                // await this.$nextTick()
                // await this.$nextTick(this.$refs.formSignIn.resetForm)
                setTimeout(this.$refs.formSignIn.resetForm, 1000);
                if (meg) console.log(meg.message);
            } catch (error) {
                alert(error.response.data.message.toString())
                //console.log(error)
            }
        },
        findUserByPhone() {
            if (this.chatRooms.length > 0) return
            if (this.searchByPhone === this.user.phone) {
                alert(this.user.phone + " l s in thoi ca bn m !");
                this.searchByPhone = ''
                return;
            }

            // useStore().clickChatMenu();

            let check = this.searchByPhone.match(/((09|03|07|08|05)+([0-9]{8})\b)/);

            if (!check) {
                alert(`S in thoi: "${this.searchByPhone}" cha hp l !`);
                return;
            }

            this.user.requestContact.forEach(contact => {
                if (contact.phone === this.searchByPhone) {
                    alert(`S in thoi: "${this.searchByPhone}" (${contact.fullName})  gi li mi kt bn cho bn !`)
                    this.searchByPhone = '';
                    check = false;
                    return;
                }
            })
            if (!check) return;
            SocketioService.findUserByPhone(this.searchByPhone);
            this.checkSearchByPhone = true;
            console.log(this.searchByPhone);
            this.currentChatRoom = {
                fullName: "",
                avatar: "",
                messages: [],
                _id: "",
            }
            this.statusContent = 'noChatSelected'
            // this.searchByPhone = ''

        },
        sendFriendRequest() {
            SocketioService.sendFriendRequest(this.foundUserByPhone.phone);
            this.searchByPhone = ""
            this.foundUserByPhone = {
                fullName: "",
                _id: "",
                avatar: "",
                phone: "",
                sentFriendRequest: false
            }
            this.statusSidebar = 'chat';
            this.statusMenu = 'chat'
            // setTimeout(useStore().reloadData(),100);
        }
    },
    beforeUnmount() {

    }

}
</script>

<style scoped>
div.note {
    text-align: justify;
    font-size: 14px;
    font-weight: 300;
}

form,
form * {
    width: 100%;
}

.form-group {
    padding-bottom: 5px;
}

.form-control {
    outline: none;
    border: 1px solid black;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 18px;
}

.error-feedback {
    font-size: 13px;
    color: red;
}

#btn-requestContact {
    width: 120px;
    background-color: #0091ff;
    border: none;
    color: white;
    border-radius: 3px;
    padding: 8px 3px;
}

.login-signup {
    margin-top: 10px;
    text-align: right;
}

/* 
.login-signup a {
    margin-left: 5px;
} */
</style>
