<template>
    <div class="content" v-if="statusContent === 'chat'">
        <div class="content_header">
            <img class="content_header-img" :src="this.currentChatRoom.avatar" alt="" />
            <span>
                <i class="fa-solid fa-circle" v-if="this.currentChatRoom.online"></i>
            </span>
            <div class="content_header-title">
                <h1> {{ this.currentChatRoom.fullNameFriend }}</h1>
            </div>
            <!-- <div class="content_header-right">
                <i class="fa fa-search" aria-hidden="true"></i>
            </div> -->
        </div>
        <div class="content_body">
            <div class="chat-box">
                <template v-for="(message, index) in this.messages" :key="index">
                    <div v-if="index === (this.currentChatRoom.messages.length - 1) && index > 2">
                        <div class="mess-incoming" v-if="message.sender !== this.user._id">
                            <div class="mess-avatar">
                                <img :src="this.currentChatRoom.avatar" alt="" />
                            </div>
                            <div class="details" v-if="message.type === 'text'" style="margin-bottom: 10px;">
                                <div class="mess-text">
                                    <div class="mess-name">{{ this.currentChatRoom.fullNameFriend }}</div>
                                    {{ message.content }}
                                    <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                                </div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'" style="margin-bottom: 10px;">
                                <img :src=message.content alt="" />
                            </div>
                        </div>
                        <div class="mess-outgoing" v-else >
                            <div class="mess-text" v-if="message.type === 'text'" :style="[((index !== this.lastSeenMessage))?'margin-bottom: 10px;':'']" :title="[(message.seen)?' xem':' gi']">
                                {{ message.content }}
                                <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'" :style="[((index !== this.lastSeenMessage))?'margin-bottom: 10px;':'']" :title="[(message.seen)?' xem':' gi']">
                                <img :src=message.content alt="" />
                            </div>
                        </div>
                        <div v-if="(index === this.lastSeenMessage)" class="mess-seen">
                            <img :src="this.currentChatRoom.avatar" alt=""/>
                        </div>
                    </div>
                    <div v-else>
                        <div class="mess-incoming" v-if="message.sender !== this.user._id">
                            <div class="mess-avatar">
                                <img :src="this.currentChatRoom.avatar" alt="" />
                            </div>
                            <div class="details" v-if="message.type === 'text'">
                                <div class="mess-text">
                                    <div class="mess-name">{{ this.currentChatRoom.fullNameFriend }}</div>
                                    {{ message.content }}
                                    <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                                </div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'" style="margin-bottom: 10px;">
                                <img :src=message.content alt="" />
                            </div>
                        </div>

                        <div class="mess-outgoing" v-else >
                            <div class="mess-text" v-if="message.type === 'text'" :title="[(message.seen)?' xem':' gi']">
                                {{ message.content }}
                                <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'" style="margin-bottom: 10px;" :title="[(message.seen)?' xem':' gi']">
                                <img :src=message.content alt="" />
                            </div>
                        </div>
                        <div v-if="(index === this.lastSeenMessage)" class="mess-seen">
                            <img :src="this.currentChatRoom.avatar" alt="" />
                        </div>
                    </div>
                </template>
            </div>
        </div>
        <div class="chat-footer">
            <div class="chat-footer">
                <button @click="openUploadModal()">
                    <i class="fa-solid fa-image"></i>
                </button>
                <form class="" @submit.prevent="sendMessageFriend('text')">
                    <input type="text" placeholder="Nhp vo ni dung bn mun nhn...." v-model="inputMessageText" />
                    <button type="submit">
                        <i class="fa-solid fa-paper-plane"></i>
                    </button>
                </form>
            </div>
        </div>
    </div>

    <div class="content" v-if="statusContent === 'chatWithStranger'">
        <div class="content_header">
            <img class="content_header-img" src="../assets/avatar-stranger.png" alt="" />
            <div class="content_header-title">
                <h1>Ngi l</h1>
            </div>
            <div class="content_header-right ">
                <!-- <i class="fa fa-search" aria-hidden="true"></i> -->
                <button id="btn-newChat" style="margin-right: 15px;" @click="connectSocketStranger()">
                    <i class="fa-solid fa-comment-medical"></i> Phng nhn tin mi
                </button>
                <button id="btn-stopChat" @click="stopChatStranger()">
                    <i class="fa-solid fa-comment-slash"></i> Kt thc
                </button>
            </div>
        </div>
        <div class="content_body">
            <div class="chat-box">
                <template v-for="(message, index) in messagesChatWithStranger" :key="index">
                    <div v-if="index === (this.messagesChatWithStranger.length - 1) && index > 2">
                        <div class="mess-incoming" v-if="message.name === 'Ngi l'">
                            <div class="mess-avatar">
                                <img src="../assets/avatar-stranger.png" alt="" />
                            </div>
                            <div class="details" v-if="message.type === 'text'" style="margin-bottom: 10px;">
                                <div class="mess-text">
                                    <div class="mess-name">{{ message.name }}</div>
                                    {{ message.content }}
                                    <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                                </div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'">
                                <img :src=message.content alt="" />
                            </div>
                        </div>
                        <div class="mess-incoming" v-else-if="message.name === 'Bot'">
                            <div class="mess-avatar">
                                <img src="../assets/avatar-bot.png" alt="" />
                            </div>
                            <div class="details">
                                <div class="mess-text" style="margin-bottom: 10px;">
                                    <div class="mess-name">{{ message.name }}</div>
                                    {{ message.content }}
                                    <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                                </div>
                            </div>
                        </div>
                        <div class="mess-outgoing" v-else>
                            <div class="mess-text" v-if="message.type === 'text'" style="margin-bottom: 10px;">
                                {{ message.content }}
                                <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'">
                                <img :src=message.content alt="" />
                            </div>
                        </div>
                    </div>
                    <div v-else>
                        <div class="mess-incoming" v-if="message.name === 'Ngi l'">
                            <div class="mess-avatar">
                                <img src="../assets/avatar-stranger.png" alt="" />
                            </div>
                            <div class="details" v-if="message.type === 'text'">
                                <div class="mess-text">
                                    <div class="mess-name">{{ message.name }}</div>
                                    {{ message.content }}
                                    <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                                </div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'">
                                <img :src=message.content alt="" />
                            </div>
                        </div>
                        <div class="mess-incoming" v-else-if="message.name === 'Bot'">
                            <div class="mess-avatar">
                                <img src="../assets/avatar-bot.png" alt="" />
                            </div>
                            <div class="details">
                                <div class="mess-text">
                                    <div class="mess-name">{{ message.name }}</div>
                                    {{ message.content }}
                                    <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                                </div>
                            </div>
                        </div>
                        <div class="mess-outgoing" v-else>
                            <div class="mess-text" v-if="message.type === 'text'">
                                {{ message.content }}
                                <div class="mess-time"><br />{{ formatTime(message.createAt) }}</div>
                            </div>
                            <div class="mess-img" v-else-if="message.type === 'image'">
                                <img :src=message.content alt="" />
                            </div>
                        </div>
                    </div>
                </template>
            </div>
        </div>
        <div class="chat-footer">
            <button @click="openUploadModal()" id="btn-sendImg">
                <i class="fa-solid fa-image"></i>
            </button>
            <form class="" @submit.prevent="sendMessageStranger('text')">
                <input id="input-sendMess" type="text" placeholder="Nhp vo ni dung bn mun nhn...."
                    v-model="inputMessageTextChatWithStranger" />
                <button id="btn-sendMess" type="submit">
                    <i class="fa-solid fa-paper-plane"></i>
                </button>
            </form>
        </div>
    </div>

    <div class="contact_main" v-if="statusContent === 'requestContact'">
        <div class="content_header">
            <i class="bx bxs-user-plus"></i>
            <div class="content_header-title">
                <h1>Danh sch kt bn</h1>
            </div>
        </div>
        <div class="contact_list">
            <template v-for="(contact, index) in user.requestContact" :key="index">
                <div class="contact_item" :title=contact.fullName>
                    <img :src=contact.avatar alt="" />
                    <div class="contact_content">
                        <h3 class="contact_name">{{ contact.fullName }}</h3>
                    </div>
                    <div class="contact_action">
                        <button class="btn btn_accept"
                            @click="this.actionFriendRequest({ phone: contact.phone, accept: true })">Chp nhn</button>
                        <button class="btn btn_deny"
                            @click="this.actionFriendRequest({ phone: contact.phone, accept: false })">T chi</button>
                    </div>
                </div>
            </template>
        </div>
    </div>

    <div class="contact_main" v-if="statusContent === 'contacts'">
        <div class="content_header">
            <i class="bx bxs-group"></i>
            <div class="content_header-title">
                <h1>Danh sch bn b</h1>
            </div>
        </div>
        <div class="contact_list">
            <template v-for="(contact, index) in user.contacts" :key="index">
                <div class="contact_item" :title=contact.fullName>
                    <img :src=contact.avatar alt="" />
                    <div class="contact_content">
                        <h3 class="contact_name">{{ contact.fullName }}</h3>
                    </div>
                </div>
            </template>
        </div>
    </div>

    <div class="contact_main introduce" v-if="statusContent === 'notSignIn'">
        <img src="../assets/redi-chat.svg" alt="">
        <h1>REDI</h1>
        <h2>XO NHO KHONG CCH</h2>
    </div>

    <div class="contact_main no-chat-selected" v-if="statusContent === 'noChatSelected'">
        <img src="../assets/redi-chat.svg" alt="">
        <p>Xin cho {{ this.user.fullName.substr(this.user.fullName.toString().lastIndexOf(" ")) }} </p>
        <p>cho mng bn n vi REDI, </p>
        <p>bt u tr chuyn vi nhng ngi m bn yu thng no !</p>
    </div>
</template>
  
<script lang="js">
import SocketioService from '../services/socketio.service.js';
// import AuthService from "../services/auth.service"
import { useStore } from '../stores/Store';
import { storeToRefs } from 'pinia'
export default {
    name: 'Content',
    props: {
        // statusContent: { type: String, require: true, default: "notSignIn" },
    },
    components: {
    },
    mounted() {
    },
    data() {
        let store = useStore();
        let { statusContent } = storeToRefs(store);
        let { chatRoomStranger } = storeToRefs(store);
        let { user } = storeToRefs(store);
        const { currentChatRoom } = storeToRefs(store);
        return {
            statusContent,
            messagesChatWithStranger: [],
            inputMessageTextChatWithStranger: '',
            url: '',
            chatRoomStranger,
            user,
            inputMessageText: "",
            currentChatRoom
        };
    },
    computed: {
        messages() {
            if (this.currentChatRoom.messages.length > 0) {
                return this.currentChatRoom.messages;
            } else {
                return [];
            }
        },
        lastSeenMessage() {
            if (this.currentChatRoom.messages.length > 0) {
                for(let i = this.currentChatRoom.messages.length-1; i>=0; i--){
                    if(this.currentChatRoom.messages[i].sender === this.user._id &&  this.currentChatRoom.messages[i].seen){
                        return i;
                    }
                }
            }

            return -1;
        }
    },
    watch: {
        statusContent(newValue, oldValue) {
            if (newValue === "chatWithStranger") {
                setTimeout(() => {
                    // const temp = localStorage.getItem('chatRoomStranger')
                    if (!this.chatRoomStranger) {
                        this.disableNewChat(false);
                        this.disableFormInput(true);
                        this.disableStopChat(true);
                    }
                    else {
                        this.disableNewChat(false);
                        this.disableFormInput(false);
                        this.disableStopChat(false);
                    }
                    console.log(oldValue);
                }, 80);
            }
        },
        messagesChatWithStranger: {
            deep: true,
            handler() {
                console.log("hey.....")
                setTimeout(() => {
                    const e = document.querySelector(".content_body")
                    e.scrollTop = e.scrollHeight * 2;
                }, 100);
            }
        }
    },
    methods: {
        autoSrcollTop() {
            setTimeout(() => {
                const e = document.querySelector(".content_body")
                e.scrollTop = e.scrollHeight * 2;
            }, 100);
        },
        sendMessageFriend(type) {
            if(!type) return;
            let message;
            if (type === "text" && this.inputMessageText !== '') {
                message = { content: this.inputMessageText, type: type, roomID: this.currentChatRoom._id };
            }
            if (type === 'image') {
                message = { content: this.url, type: type, roomID: this.currentChatRoom._id };
                this.url = ''
            }
            if(!message) return;
            console.log("sending message....");
            SocketioService.sendMessageFriend(message, cb => {
                // callback is acknowledgement from server
                console.log(cb);
                this.currentChatRoom.messages.push({
                    ...message,
                    createAt: cb.createAt,
                    sender: this.user._id,
                    seen: false
                });
                this.autoSrcollTop()
                // clear the input after the message is sent
                this.inputMessageText = '';
            });
        },
        openUploadModal() {
            window.cloudinary.createUploadWidget(
                {
                    cloud_name: 'dwnunieno',
                    upload_preset: 'ma9pfovj',
                    maxImageFileSize: 10000000,
                    autoMinimize: true,
                    sources: ["local"],
                    clientAllowedFormats: ["image"],
                    maxFiles: 5
                },
                (error, result) => {
                    if (!error && result && result.event === "success") {
                        console.log('Done uploading..: ', result.info);
                        this.url = result.info.url;
                        console.log(this.url)
                        if (this.currentChatRoom._id === '000') {
                            this.sendMessageStranger('image');
                        } else {
                            this.sendMessageFriend('image');
                        }
                    }
                }).open();

            // widget.destroy({ removeThumbnails: true })
            //     .then(() => {
            //         console.log("Widget was destroyed");
            //     });

        },
        connectSocketStranger() {
            setTimeout(() => {
                const chatRoom = this.chatRoomStranger;
                console.log(chatRoom)
                if (chatRoom !== '') {
                    this.messagesChatWithStranger = [{ content: "ang i ngi l ...", name: "Bot" }];
                    //this.stop = false; this.next = false;
                    // this.submitToken();
                    this.disableNewChat(true);
                    this.disableFormInput(true);
                    this.disableStopChat(false);
                    SocketioService.nextRoomStranger(chatRoom);
                }
                else {
                    SocketioService.setupSocketStrangerConnection();

                    SocketioService.statusRoomStranger((err, data) => {
                        console.log(data);
                        if (data.content === 'ang i ngi l ...') {
                            this.disableNewChat(true);
                            this.disableFormInput(true);
                            this.disableStopChat(false);
                        }
                        if (data.content.includes("NextRoom")) {
                            data.content = "Ngi l  ri i. ang i ngi l ..."
                            // this.stop = false; this.next = false;
                            // this.disableFormElements("form.input-div", true);
                            this.disableNewChat(true);
                            this.disableFormInput(true);
                            this.disableStopChat(false);
                            this.setChatRoomStranger('');
                        }
                        else if (this.messagesChatWithStranger.length > 0) {
                            this.messagesChatWithStranger = [];
                        }
                        if (data.content.includes("Ngi l  vo phng")) {
                            const chatRoom = data.content.substr(data.content.indexOf("|") + 1, data.content.length - 1);
                            this.setChatRoomStranger(chatRoom)
                            data.content = data.content.substr(0, data.content.indexOf("|"));
                            this.disableNewChat(false);
                            this.disableFormInput(false);
                            this.disableStopChat(false);
                            // this.disableFormElements("form.input-div", false);
                            // this.stop = true; this.next = true;
                        }
                        if (data.content === 'Ngi l  ri i. ang i ngi l k tip ...') {
                            // this.stop = false; this.next = false;
                            // this.disableFormElements("form.input-div", true);
                            this.disableNewChat(true);
                            this.disableFormInput(true);
                            this.disableStopChat(false);
                        }
                        // console.log(data.name)
                        this.messagesChatWithStranger.push({
                            name: data.name,
                            content: data.content,
                            type: "text",
                            createAt: data.createAt
                        });
                    });
                    SocketioService.receiveMessageStranger((err, data) => {
                        console.log(data);
                        this.messagesChatWithStranger.push(data);
                    });
                }
            }, 80);
        },
        sendMessageStranger(type) {
            if(!type)   return;
            
            let message;
            if (type === "text" && this.inputMessageTextChatWithStranger !== '') {
                message = { content: this.inputMessageTextChatWithStranger, type: type };
            }
            if (type === 'image') {
                message = { content: this.url, type: type };
                this.url = ''
            }
            if(!message) return;
            SocketioService.sendMessageStranger(message, cb => {
                // callback is acknowledgement from server
                console.log(cb);
                this.messagesChatWithStranger.push({
                    ...message,
                    createAt: cb.createAt,
                    name: "Me"
                });
                // clear the input after the message is sent
                this.inputMessageTextChatWithStranger = '';
            });
        },
        stopChatStranger() {
            setTimeout(() => {
                SocketioService.disconnectStranger();
                this.messagesChatWithStranger.push({
                    type: "text",
                    createAt: this.getTime(),
                    content: "Bn  ri khi phng !",
                    name: "Bot"
                });
                // this.disableFormElements("form.input-div", true);
                this.setChatRoomStranger('')
                this.disableNewChat(false);
                this.disableFormInput(true);
                this.disableStopChat(true);
                // clear the input after the message is sent
                // this.stop = false; this.next = false;
                this.inputMessageTextChatWithStranger = '';
            }, 69)
            this.messagesChatWithStranger = [];
        },
        // getTime() {
        //     let today = new Date();
        //     let date = today.getDate() + '-' + (today.getMonth() + 1) + '-' + today.getFullYear();
        //     let time = today.getHours() + ":" + today.getMinutes();
        //     let dateTime = time + ' ' + date;
        //     return dateTime;
        // },
        getTime() {
            var d = new Date();
            var utc = d.getTime() + (d.getTimezoneOffset() * 60000); // UTC time
            var offset = 7; // UTC +7 hours
            var gmt7 = new Date(utc + (3600000 * (offset - d.getTimezoneOffset() / 60)));
            return gmt7;
            // let str = gmt7.toLocaleString('vi-VN', { timeZone: 'UTC' }).split(" ");
            // return str[0].substr(0,str[0].lastIndexOf(":")) + " " + str[1];
        },
        formatTime(time){
            let str = time;
            if(typeof time === "string"){
                str = new Date(time);
                // console.log(str.toLocaleString('vi-VN', { timeZone: 'UTC' }).split(" "));
            }
            str = str.toLocaleString('vi-VN', { timeZone: 'UTC' }).split(" ");
            // console.log(str)
            return str[0].substr(0,str[0].lastIndexOf(":")) + " " + str[1];
        },
        setChatRoomStranger(roomName) {
            useStore().setChatRoomStranger(roomName);
        },
        disableNewChat(bDisabled) {
            document.querySelector('#btn-newChat').disabled = bDisabled;
        },
        disableFormInput(bDisabled) {
            document.querySelector('#btn-sendImg').disabled = bDisabled;
            document.querySelector('#btn-sendMess').disabled = bDisabled;
            document.querySelector('#input-sendMess').disabled = bDisabled;
        },
        disableStopChat(bDisabled) {
            document.querySelector('#btn-stopChat').disabled = bDisabled;
        },
        actionFriendRequest(data) {
            SocketioService.actionFriendRequest(data)
            // setTimeout(useStore().reloadData(),900);   
        }
    },
    beforeUnmount() {
        SocketioService.disconnectStranger();
        SocketioService.disconnect();
    }

}
</script>

